body {
    font-family: "Roboto", sans-serif;
    color: #424242;
}

/*header nav*/
.logo h2 {
    margin-left: 15%;
    color: white;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
}

.nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    width: 100%;
}

nav ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 60px 70px 110px;
    justify-content: right;
    gap: 1rem;
}

nav ul li a {
    color: white;
    display: inline-block;
    padding: 10px;
    text-decoration: none;
    transition: box-shadow .2s ease-in-out;
}

nav ul li a:hover {
    display: inline-block;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    padding: 10px;
    background-color: #EDF1D6;
    border-radius: 5px;
    text-decoration: none;
    color: #000000;
}

/*hamburger*/
.hamburger {
    display: none;
    z-index: 99;
    position: fixed;
    top: 16px;
    right: 35px;
    min-width: 44px;
    min-height: 44px;
    color: rgb(230, 171, 61);
}

.hamburger div {
    width: 25px;
    height: 2px;
    background-color: #000;
    margin: 7px;
    transition: all 0.3s;
    z-index: 99;
}

